import config from "../config";

// Función simple para manejar logs
const log = (...args) => {
  if (config.enableLogs) {
    console.log(...args);
  }
};

// Función para errores (siempre se muestran)
log.error = (...args) => {
  console.error(...args);
};

// Función para advertencias
log.warn = (...args) => {
  if (config.enableLogs) {
    console.warn(...args);
  }
};

// Función para información
log.info = (...args) => {
  if (config.enableLogs) {
    console.info(...args);
  }
};

// Función para depuración
log.debug = (...args) => {
  if (config.enableLogs) {
    console.debug(...args);
  }
};

export default log;
